import React, { useRef } from 'react'
import { Link } from 'react-router-dom'
import logo from "../assits/logo.png"
import { motion } from "framer-motion"

export default function Root() {

  const a = useRef()

  const animate = {
    initial: {
      opacity:0,
      y:50
    },
    animation: {
      opacity:1 ,
      y:0
    },
    lol: {
      opacity:0 ,
      y:-50
    }
  }


  return (
    <motion.div 
    exit={{
      x:"-100vw"
    }}
    transition={{
      type:"just"
    }}
    className='root'
    >

      <div className='main'>

        <motion.img
        variants={animate}
        initial="lol"
        animate="animation"
        transition={{delay:.2,type:"just" }}
        src={logo} alt="logo" />

        <motion.div 
        variants={animate}
        initial="initial"
        animate="animation"
        transition={{
          type:"just",
         scale:{duration:.2}
        }}
        whileHover={{
          scale:1.1
        }}
        whileTap={{
          scale:.9
        }}
        
        onClick={() => a.current.click() }><span/><button><Link ref={a} to="weather"> </Link></button></motion.div>
        
      </div>
    </motion.div>
  )
}
