import day from "../assits/time-day.png"
import dark from "../assits/time-dark.png"
import { timeContext } from "./App"
import { useContext } from "react"


export default function Theme() {
  const ifTime = 12
  const time = useContext(timeContext)

  document.body.style.backgroundColor = time.hour > ifTime ? "#4155c8" : "#eae2b7"

  const timeStyle = {
    backgroundImage: window.innerWidth > 750 ? time.hour > ifTime ? `url(${dark})`: `url(${day})` : time.hour > ifTime ? "linear-gradient(35deg, #66c4d5 , #003049 35%)" : "linear-gradient(35deg, #f77f00 10%, #d62828 50%)"
  }
  const style1 = {
    backgroundColor:time.hour > ifTime ? "#66c4d5" : "#d62828"
  }
  const style2 = {
    backgroundColor:time.hour > ifTime ? "#1c6e8c" : "#f77f00"
    
  }
  const style3 = {
    backgroundColor:time.hour > ifTime ? "#003049" : "#fcbf49"
  }

  return (
    <>
    <h2 className='time' style={timeStyle} >{time.hour > ifTime ? time.hour - ifTime : time.hour === 0 ? ifTime : time.hour}:{time.minute < 10 ? "0" + time.minute : time.minute}</h2>

    <div className='theme'>
      <span style={style1}></span>
      <span style={style2}></span>
      <span style={style3}></span>
    </div>
    </>
  )
}
