import React, { useEffect, useState } from 'react';
import Weather from "./Weather"
import { Temporal } from '@js-temporal/polyfill';
import { Route, Routes, useLocation } from 'react-router-dom'; 
import Root from "./Root"
import { AnimatePresence } from 'framer-motion';


export const weatherOb = React.createContext()
export const timeContext = React.createContext()

function App() {
  
  // useCOnten https://api.openweathermap.org/data/2.5/find?id=524901&units=metric&country=sa&lang=ar&q=${city}&appid=6362937d262cd8b3ffed305bf38c075d
  const [city, setCity] = useState("")
  const [weather, setWeather] = useState("loading")
  
  
  useEffect(() => {
    fetch(`https://api.openweathermap.org/data/2.5/find?id=524901&units=metric&country=sa&lang=ar&q=${city}&appid=6362937d262cd8b3ffed305bf38c075d`)
    .then(res => res.json())
    .then(res => {
      setWeather(res)
    }) 
    .catch(err => console.log(err))
    console.log(city);
  },[city])

  
  function changeCity(cityName) {
    if (cityName.length > 1) {
      setCity(cityName)
    } 
  }

  const time = new Temporal.Now.plainDateTimeISO()

  const location = useLocation()

  return (

    <div className="App">
      <timeContext.Provider value={time}>
      <weatherOb.Provider value={weather}>
        <AnimatePresence exitBeforeEnter={true}>
          <Routes location={location} key={location.pathname}> 
            <Route  path='/' element={<Root />}/>
            <Route path='weather' element={<Weather changeCity={changeCity}/>} /> 
          </Routes>
        </AnimatePresence>
      </weatherOb.Provider>
      </timeContext.Provider>
    </div>
  );
}

export default App;
